:root {
  --primaryColor: #00eaff;
  --primaryHover: hsl(185, 92%, 41%);
  --secondaryColor: rgb(150, 162, 188);
  --paragraphColor: rgb(127, 127, 175);
  --bgColor: #031a30;
  --formColor: rgb(2, 15, 29);
  --projectCardColor: #072340;
  --textColor: #dae4fb;
  --headerHeight: 3rem;
}

.about {
  .sectionContent {
    .textSection {
      h4 {
        color: var(--secondaryColor);
        max-width: 100%;
        font-size: 1.1rem;
        letter-spacing: 1px;
        font-weight: 500;
        margin: auto;
      }

      .aboutBtn {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
        width: max-content;
        background: var(--primaryColor);
        padding: 10px 15px;

        &:hover {
          background: var(--primaryHover);
        }

        a {
          color: var(--projectCardColor);
          font-size: 1.2rem;
          font-weight: 600;
          cursor: pointer;
          gap: 0.5rem;
        }

        a:hover {
          color: var(--textColor);
        }
      }
    }

    .aboutImgDiv {
      position: relative;
      width: 250px;
      height: 250px;
      margin: 0.5rem auto 3rem;
      border-radius: 50%;
      overflow: hidden;
      display: grid;
      border: 2px solid var(--primaryColor);

      aboutImg {
        width: 215px;
        object-fit: cover;
        align-self: center;
        justify-self: center;
      }
    }

    // .aboutImgDiv::before {
    //   font-size: 1.6rem;
    //   color: var(--bgColor);
    //   text-align: center;
    //   display: flex;
    //   align-items: center;
    //   justify-content: center;
    //   font-family: "Inconsolata", monospace;
    //   justify-self: center;
    //   align-self: center;
    //   background: var(--primaryColor);
    //   height: 97%;
    //   width: 97%;
    //   position: absolute;
    //   border-radius: 50%;
    //   overflow: hidden;
    //   transition: all 0.3rem;
    //   mix-blend-mode: screen;
    // }

    // .aboutImgDiv:hover.aboutImgDiv::before {
    //   height: 0%;
    // }
  }
}

// MEDIA QUERIES //
@media screen and (min-width: 769px) {
  .about {
    width: 70%;
    margin: auto;
  }
}

@media screen and (min-width: 960px) {
  .about {
    .sectionContent {
      grid-template-columns: repeat(2, 1fr);
      justify-content: center;
      margin: auto;
    }
  }
}

@media screen and (min-width: 1240px) {
  .about {
    .sectionContent {
      .textSection {
        h4 {
          font-size: 1.2rem;
        }
      }
    }
  }
}
